<template>
  <div>
    <vue-particles
      color="#00B2EE"
      :particle-opacity="0.7"
      :particles-number="80"
      shape-type="circle"
      :particle-size="4"
      lines-color="#00E5EE"
      :lines-width="1"
      :line-linked="true"
      :line-opacity="0.3"
      :lines-distance="150"
      :move-speed="3"
      :hover-effect="true"
      hover-mode="grab"
      :click-effect="true"
      click-mode="push"
    />

    <div id="outer">
      <el-container>
        <el-header><span class="text">{{ str }}</span></el-header>
        <el-main>
          <el-row type="flex" class="row-bg" justify="center">
            <el-col :span="24"><div class="content">
                                 <el-input
                                   v-model="account"
                                   placeholder="请输入账号："
                                   clearable
                                 />
                               </div>
              <el-input v-model="password" placeholder="请输入密码：" show-password />

            </el-col>
          </el-row>
          <el-row type="flex" class="row-bg" justify="center">
            <el-col :span="24"><a href="javascript:0;" class="btn" @click="submit()">
              登入</a>
            </el-col>
          </el-row>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      account: '',
      password: '',
      i: 0,
      timer: 0,
      str: '',
      str1: '欢迎登入MARKET'
    }
  },
  watch: {
    i(val, oldVal) {
      if (val === this.str1.length + 1) {
        this.typingSub()
      }
    }
  },
  created() {
  },
  mounted() {
    window.addEventListener('resize', this.getHeight)
    this.getHeight()
    this.typing()
  },
  methods: {
    getHeight() {
      // this.conheight.height = window.innerHeight - 170 + 'px'
      // console.log(window.innerHeight + 'px')
      // console.log(document.getElementById('particles-js').style.height)
      document.getElementById('particles-js').style.height = window.innerHeight + 'px'
    },
    typing() {
      if (this.i <= this.str1.length) {
        this.str = this.str1.slice(0, this.i++) + '_'
        this.timer = setTimeout(this.typing, 250)
      } else {
        console.log(this.i)
        this.str = this.str1
        clearTimeout(this.timer)
      }
    },
    typingSub() {
      if (this.i > 0) {
        this.str = this.str1.slice(0, this.i--) + '_'
        this.timer = setTimeout(this.typingSub, 250)
      } else {
        this.str = '_'
        // this.str = this.str1.slice(-1, this.i--) + '_'
        clearTimeout(this.timer)
      }
    },
    submit() {
      console.log(this.account, this.password)
      // this.$message({
      //   showClose: true,
      //   message: '账号或密码错误！',
      //   type: 'error'
      // })
      this.$router.push({ path: '/home' })
      console.log(screen.height)
    }
  }
}
</script>

<style scoped>

body,html{
  margin: 0 !important;
  /* overflow: hidden; */
}
#app{
  height: 100%;
}
.el-main .btn{
  display: inline-block;
  width: 100%;
  height: 80px;
  font-size: 20px;
  margin-top: 130px;
  text-decoration: none;
  color: ghostwhite;
  background-image: linear-gradient(
90deg
,#00B2EE,#00BFFF ,#00E5EE);
  text-align: center;
  line-height: 80px;
  border-radius: 40px ;
}
.el-main .btn :active{
  background-color: red;

}
</style>
<style>
body{
  margin: 0 !important;
  overflow: auto;
}
#particles-js{
  background: url('../../../public/background.webp') left center;
  background-size: cover;
  width: 100%;
  height: 20rem;
  position: relative;
}
#outer{
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: auto;
}
.el-header, .el-footer {
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-header{
  margin-top: 100px
}
.el-header span{
  display: inline-block;
  height: 120px;
  width: 100%;
  line-height: 120px;
  font-size: 30px;
  color:#28c9ff
}
.el-main {
  color: #333;
  text-align: center;
  line-height: 120px;
  padding: 60px;
}

</style>
